@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barlow:800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/portfolio/static/media/diagonals.6a94fd4c.jpeg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#root, #main {
    height: 90vh;
    padding-bottom: 40px;

}
.title, .lina {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #2a9d8f;
    font-size: 1vw;
    font-family: 'Raleway', sans-serif;
}

.lina {
    margin-bottom: 40px;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    color: #caf0f8;
    font-weight: bold;
    background-color: black;
    height: 60px;
    font-family: 'MuseoModerno', cursive;
    margin-bottom: 30px;
}

.nav-bar-item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    list-style: none;
    padding-right: 60px;
    text-decoration: none;
    color: #caf0f8;
}

.nav-bar-link {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    list-style: none;
    padding-left: 60px;
    text-decoration: none;
    color: #caf0f8;
}


.carousel {
    margin-left: 10vh;
    margin-right: 10vh;
}

.image {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: auto;
    height: 60vh;
    margin: auto;
}




#main-container {
    margin-left: 300px;
    margin-right: 300px;
    margin-top: 50px;
}

.main-about {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #f4a261;
    text-align: justify;
    line-height: 1.8;
    flex-direction: column;
    font-size: large;
    font-weight: 300;

}

.cc-grad {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #00f5d4;
    margin: 50px;
    text-decoration: none;
}

a:hover {
    color: #ff0054;
  }
  
a:active {
    color: black;
}

.slick-prev:before,
.slick-next:before {
    color: #fdfcdc; 
    font-size: 65px;
}


  

#main-projects {
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    margin-left: 100px;
    margin-right: 100px;
    padding-bottom: 40px;
}

.link {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    text-transform: uppercase;
    width: 100%;
}

.link-title {
    text-decoration: none;
    color: #e76f51;
}

.project-card {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-start;
    font-size: large;
    color: #e76f51;
    margin: 3%;
    height: auto;
    position:relative;

}

.project-descr {
    align-content: flex-start;
    text-transform: none;
    text-align: justify;
    text-justify: inter-word;
    line-height: 1.7;
    height: 25%;
}

.tech-stack-title {
    margin-right: 50px;
    margin-left: 50px;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-start;
    width: 100%;
    color: #00f5d4;
}

.tech-stack {
    margin-right: 50px;
    margin-left: 50px;
    line-height: 1.7;
    color: #00f5d4;
}

.image1, .image2, .image3 {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    margin-top: 40px;
    border: 1px solid #000;
    height: 400px;
    width: 500px;
}

#travel {
    content: url(/portfolio/static/media/travel.d7c585dd.jpeg);
    height: 400px;
    width: 500px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

#weather {
    content: url(/portfolio/static/media/weather.357eb06d.jpeg);
    height: 400px;
    width: 500px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

#baldur {
    content: url(/portfolio/static/media/baldur.3deb04d0.jpeg);
    height: 400px;
    width: 500px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
#travel:hover, #weather:hover, #baldur:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    height: 400px;
    width: 500px;
}
footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    font-size: small;
    height: 2.5rem;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    clear: both;
    margin-top: 50px;
}
